// Header
.site-nav {
  text-align: center;

  ul {
    margin: 1em 0;
    list-style: none;
  }
  li {
    display: inline-block; //this makes it horizontal
    margin: 0 0.5em;
    font-size: 1.5em;
  }
}

.site-title a {
  color: #000;

  &:hover {
    text-decoration: none;
  }
}
.site-header {
  padding: 2em 0 0;
  border-bottom: 1px solid #ccc;
  background: url('/images/header-bg2.png');
  background-size: 400px 400px;
}

// Footer

.site-footer {
  padding: 2em 0;
  color:#fff;
  background:$grey-color-dark
}
.footer-content {
  float:left;
  width: 40%;
  font-size: 0.875em;

  h3 {
    text-align: left;
  }
}

.footer-social {
  float: right;
  li {
    display: inline-block;
    margin: 0 0.5em;
  }
}

// sections
.site-section {
  @extend %clearfix; //the same as applying clearfix class but using sass to @extend
  padding-top: $h1-font-size;
}
.site-section-last {
  padding-bottom: $h1-font-size;
}